
	import Search from 'vux/src/components/search/index.vue'

	export default{
		name: 'search',
    	components: {
		    Search
		},
		data () {
		    return {
		    	results: [],
		    	value:''
		    	
		    }
	    },
	    //实例已经创建完成之后
	    created(){
          this.$parent.init();
		},
	    //方法对象
	    methods:{
		    getResult (val) {
		      this.results = val ? getResult(this.value) : []
		    },
		    inputchange(){
		    	console.log( this.results)
		    	this.$http.post(this.$root.url+"basedata/queryModels", {
		    		"keywords":this.value	
	    		}, {
			        headers: {},
			        emulateJSON: true
			    }).then(function(data) {// 这里是处理正确的回调
			    	console.log(data)
			        var datas=data.body.data;
	            	this.results=datas.data;
			        console.log( this.results)
			    }, function(error) {// 这里是处理错误的回调
			        console.log(error)
			    });
		    }
		  	
		},
		
		//计算属性
		computed:{
        
        }
	    
	}
	function getResult (val) {
		  let rs = []
		  for (let i = 0; i < 8; i++) {
		    rs.push({
		      title: `${val} result: ${i + 1} `,
		      other: i
		    })
		  }
		  return rs
		}
	



