
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import cookie from '../../config/cookie' 
/*组件*/
export default {
  	name: 'myQcode',
  	created () {
  		this.$parent.init();
  		
        if (typeof WeixinJSBridge == "undefined"){
	        if( document.addEventListener ){
	            document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
	        }else if (document.attachEvent){
	            document.attachEvent('WeixinJSBridgeReady', onBridgeReady); 
	            document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
	        }
        }else{
        	onBridgeReady();
        }
	    function onBridgeReady(){
	        WeixinJSBridge.call('showOptionMenu');
	    }
  	},
	mounted () {
		var phone = this.$route.query.phone?this.$route.query.phone:'';
		var vm = this;
        this.$http.post(this.$root.url+"userCenter/getQrCodeUrl",{
        	phoneNo:phone
        }).then(function(res) {//success
           console.log('res',res);
           if(res.ok&&res.body.ask==0){
              	vm.imgBase = 'data:image/png;base64,'+res.body.data.img
           }else{
              this.showTips = true;
              this.showTipsMsg = res.body.message;
           }
        }, function(res) {// fail
           this.showTips = true;
           this.showTipsMsg = res.statusText;
        });

	},
    components: {
	    Cell,
	    XButton,
	    Popup,
	    XSwitch
  	},
    data () {
	    return {
	       url:"",
	       show:false,
	       imgBase:''
	    }
	},
	//方法
    methods:{
	  	saveOrUpdatePlan:function(e){
	  		this.show = true
	  	},
	  	popupMaskClick:function(e){
	  		this.show = false
	  	}
	}
}



