
import Icon from 'vux/src/components/icon/index.vue'

import btn3 from '../widget/buttom-3'
export default {
  components: {
    Icon,
    'btn3': btn3
  },
  data () {
    return {
      step1: 1,
      step2: 0
    }
  },
  methods: {
    nextStep () {
      this.step2 ++
    }
  },
  created () {
    this.$parent.init();
  }
}



