
import Btn2 from '../widget/buttom-2'
import Icon from 'vux/src/components/icon/index.vue'

export default {
  props: {

  },
  components: {
    Icon,
    'btn2': Btn2
  },
  data () {
    return {
      iconType:false,
      mainData:{},
      checkData:{},
      orderId:this.$route.query.orderId,
      challenge:'',//挑战码
      cvvInsur:''//是否支付商业险：1支付，0没有支付
    }
  },
  methods: {
    btn11:function(){
      this.$router.push({path:'/installmentInfo',query: {}})
    },
    btn22:function(){
      this.checkData.phoneNum=this.mainData.phoneNo;
      this.checkData.contractNo='14903';
      this.checkData.contractType='20';
      this.checkData.isSeal=0;
      this.checkData.challengeCode=this.challenge;
      this.$http({
        method: 'GET',
        url: this.$root.url + "ca/checkChallengeCode",
        params: this.checkData,
        headers: {'Content-Type': 'application/json;charset=UTF-8'},
      }).then(function (res) {
        if(res.data.ask==0){
          this.$router.push({path:'/installmentSuccess',query: {cvvInsur:this.mainData.cvvInsur}})
        }

      }, function (error) {
        console.log(2)
      })

    },
    isAgree(){
      this.iconType=!this.iconType;
    },
    getChallengeCode(){
      this.$http({
        method: 'GET',
        url: this.$root.url + "ca/getChallengeCode",
        params: {
          'certId':this.mainData.idCard,
          'phoneNum':this.mainData.phoneNo,
          'customerName':this.mainData.vehicleName,
          'contract':'14903',
          'contractType':'01'
        },
        headers: {'Content-Type': 'application/json;charset=UTF-8'},
      }).then(function (res) {
        if(res.data.ask==0){


          this.checkData.transId=res.data.data.transID;
        }

      }, function (error) {
        console.log(2)
      })
    },
    _init(){
      this.$http({
        method: 'GET',
        url: this.$root.url + "order/basicContractInfo",
        params: {'orderId':this.orderId},
        headers: {'Content-Type': 'application/json;charset=UTF-8'},
      }).then(function (res) {
        if(res.data.ask==0){
          this.mainData=res.data.data;
          this.cvvInsur=res.data.data.cvvInsur
        }

      }, function (error) {
        console.log(2)
      })

    }
  },
  created:function(){
    this.$parent.init();
    this._init()
    this.$root.token='1123123'

  }

}





