
	import Selector from 'vux/src/components/selector/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

	import cookie from '../../config/cookie' 
	
	export default{
		name: 'datum',
    	components: {
		    Selector,
		    XSwitch,
		    XAddress,
		    Alert
		}, 
		data () {
		    return {
		        isChange:true,//是否上牌
		        shengshiname:[],//省市名称
		        addressData: [],//省、市列表数据
		        provinceCode:[],//省、市编码
		        provinceCodes:'',  //投保省份编码
				province:'',  // 投保省份
				cityCode:'', // 投保城市编码
				city:'', //投保城市
				areaCode:'',  // 投保区域编码（选填）
				area:'',// 投保区域 （选填）
				driverLicense:'', // 车牌号  选填
				vehicleSign:'', // 辆是否已上牌（0、已上牌，1、未上牌，默认0）
				name:'',   // 投保人 
				cardNo:'',  // 投保人
				token:'',//获取token
		        alertShow:false,//错误提示框
        		alertTxt:'',//错误提示语
		    }
	    },
	    //实例已经创建完成之后
	    created(){
           this.$parent.init();
		},
		//组件写入dom结构之前
		mounted(){
            var vms=this
            //请求投保城市列表
            this.token=cookie.Cookie.get("token")?cookie.Cookie.get("token"):'';
            
            vms.$http.post(this.$root.url+"basedata/getAreaInfo", {
    		}, {
		        headers: {'Content-Type': 'application/json;charset=UTF-8',"token":this.token},
		    }).then(function(data) {// 这里是处理正确的回调
		        var datas=data.body.data;
		        if(datas&&datas.length>0){
            		vms.addressData=datas
        		}
		    }, function(error) {// 这里是处理错误的回调
		        console.log(error)
		    });
		},
	    //方法对象
	    methods:{
		    //选中开户省市时调用方法
			addreChange(va){
				//获取支行列表
				var _this = this;
				this.shengshiname = getaddres(this.provinceCode,this.addressData);
			},
			//上一步
			back(){
				window.history.go(-1)
			},
		    //下一步提交
		    datumbuts(){
		    	console.log(this.shengshiname);
		    	if(this.provinceCode.length<=0){
		    		this.alertTxt="投保城市没选择";
				    this.alertShow=true
				    return false
		    	}else if(this.name==""){
		    		this.alertTxt="车主姓名不能为空";
				    this.alertShow=true
				    return false
		    	}else if(this.cardNo==""){
		    		this.alertTxt="车主身份号不能为空";
				    this.alertShow=true
				    return false
		    	} 
		    	var group = {
		    	 	"province":this.shengshiname.length>0?this.shengshiname[0].name:"",//省名称
		    	 	"provinceCode":this.provinceCode[0],//省编码
					"city":this.shengshiname.length>0?this.shengshiname[1].name:"",//市名称
					"cityCode":this.provinceCode[1], //市编码
					"areaCode":this.areaCode,//区编码
					"area":this.area, //区
					"driverLicense":this.isChange==false ? this.driverLicense : '', //车牌号
					"vehicleSign":this.Changeval,   //是否上牌
					"name":this.name,   //车主姓名
					"cardNo":this.cardNo,//车主身份号
		    	}
		    	
		    	this.$http.post(this.$root.url+"/apply/applyPolicy",group , {
			        headers: {'Content-Type': 'application/json;charset=UTF-8',"token":this.token},
			    }).then(function(data) {// 这里是处理正确的回调
			        if(data.body.ask==0){
			        	this.$router.push({path:'/yesbrand',query: {"Changeval":this.Changeval}})//其中login是你定义的一个路由模块
			        }
			    }, function(error) {// 这里是处理错误的回调
			        console.log(error)
			    });
		    }
		},
		//计算属性
		computed:{
           Changeval(){
           	return this.isChange==true?"1":"0"
           }
        }
	    
	}
	//获取省市的名称
	function getaddres(st,arrs){
		var addobj = [];
		for(var i=0;i<arrs.length;i++){
			if(arrs[i].value==st[0]||arrs[i].value==st[1]){
				addobj.push({
					name:arrs[i].name,
					parent:arrs[i].parent,
					id:arrs[i].value
				});
			}
		}
		return addobj;
	}



