
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import noData from '../noData'

/*组件*/
export default {
  	name: 'inviteRecode',
    components: {
	    Cell,
	    Scroller,
	    LoadMore,
	    noData
  	},
    data () {
	    return {
			onFetching: false,
			list:[],
			noDataTxt:'暂无邀请数据',
			noDataImgSrc:require('../../assets/no_data.png')
	    }
	},
	//实例已经创建完成之后
	created(){
		this.$parent.init();
	},
    mounted () {
	    this.$http.post(this.$root.url+"userCenter/getInviterList",{
           emulateJSON: true
        }).then(function(res) {//success
           console.log('res',res);
           if(res.ok&&res.body.ask==0&&res.body.data){
	           	for(var i=0;i<res.body.data.length;i++){
					this.list.push({
						icon:res.body.data[i].avatarUrl,
						phone:res.body.data[i].phoneNo.substring(0,3)+'****'+res.body.data[i].phoneNo.substring(res.body.data[i].phoneNo.length-4,3),
						date:res.body.data[i].registerDate
					});
	           	}
           }else{
                this.showTips = true;
                this.showTipsMsg = res.body.message;
           }
        }, function(res) {// fail
           this.showTips = true;
           this.showTipsMsg = res.statusText;
        });
    },
    methods: {
	    onScrollBottom () {
	      /*if (this.onFetching) {
	        // do nothing
	      } else {
	        this.onFetching = true
	        setTimeout(() => {
	          this.list.push({
				icon:'../../assets/inviteUser.png',
				phone:'134****12345',
				date:'2017/01/01-12:12:12'
			});
	          this.$nextTick(() => {
	            this.$refs.scrollerBottom.reset()
	          })
	          this.onFetching = false
	        }, 2000)
	      }*/
	    }
    }
}



