
	import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'

  export default {
    name: 'app',
    components: {
  	    XHeader,
  	    Tabbar,
  	    TabbarItem,
        Loading
  	  },
    data(){
      return {
        loadingB:false,
        index:0,
        urlId:'',
        isLogin:true,
      }
    },
    beforeCreate:function(){
    },
    //获取路由上的值
  	created(){
      //this.init();
  	},
  	beforeMount(){ 
  	},
    mounted:function(){
      //:active
      document.body.addEventListener('touchstart', function () {})
      //日期格式化
      Date.prototype.Format = function (fmt) {
        var o = {
          "M+": this.getMonth() + 1, //月份
          "d+": this.getDate(), //日
          "h+": this.getHours(), //小时
          "m+": this.getMinutes(), //分
          "s+": this.getSeconds(), //秒
          "q+": Math.floor((this.getMonth() + 3) / 3), //季度
          "S": this.getMilliseconds() //毫秒
        };
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
          if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
      }
      /*调用：
       var time1 = new Date().Format("yyyy-MM-dd");
       var time2 = new Date().Format("yyyy-MM-dd HH:mm:ss");*/
    },
    methods:{
      init(){
        this.hideOrShowShareBtn();
        this.hideOrShowMenuBar();
      },
      hideOrShowShareBtn(){
        var path = this.$route.path;
        if (typeof WeixinJSBridge == "undefined"){
          if( document.addEventListener ){
              document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
          }else if (document.attachEvent){
              document.attachEvent('WeixinJSBridgeReady', onBridgeReady); 
              document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
          }
        }else{
          onBridgeReady();
        }
        function onBridgeReady(){
          WeixinJSBridge.call('hideOptionMenu');
        }
      },
      hideOrShowMenuBar(){
        var path = this.$route.path;
        if(path=="/activity"){
          this.index = 1      // 活动
        }else if(path=="/personal" || path=="/message" || path=="/altertel" || path=="/applyModify" || path=="/cardModfy" || path=="/account" || path=="/billist" || path=="/repaymentBill" || path=="/loanContract" || path=="/prepayment" || path=="/myloan" || path=="/inviter" || path=="/news"){
          this.index = 2      //个人中心
        }else{
          this.index = 0     //首页
        }
        
        if(path=="/login" || path=="/login2" || path=="/forgetPasw" || path=="/register" || path=="/myQcode"){
          this.isLogin = false
        }else{
          this.isLogin = true
        }
      }
    }
  }



